.imgfilmsmall {
  width: 220px;
  margin-left: 5px;
  border-style: solid;
  border-color: rgb(76, 0, 0);
  border-radius: 15px;
}
.imgfilmsmall:hover {
  border-color: rgb(228, 136, 136);
}
.rowbox {
  margin-top: 13px;
  overflow-x: scroll;
  overflow-y: hidden;
  display: flex;
}
::-webkit-scrollbar {
  height: 8px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(224, 0, 0);
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  background: rgb(248, 185, 185);
  border-radius: 20px;
}
#title1{
  font-size: small;
  text-align: center;
}
